    // };

    // watchEffect(() => {
    //   if (swiper.value) {
    //     swiper.value.update();
    //   }
    // });
    return {
      rotateRadiator,
      rotateRadiatorEnabled,
      // goToPrevSlide,
      goToSlide,
    };
  },
};
