<template>
  <div class="h-screen flex">
    <div class="card-container flex">
      <div class="card" v-if="cardFront != null" :cardId="state.cardId">
        <div
          class="card-inner flex items-center w-full"
          @click="rotateHorizontal"
          v-bind:class="{
            flipme: state.cardOne == 'flipped',
            rovescia: state.cardVertical == 'rovesciata',
          }"
        >
          <div class="card-face card-front">
            <img :src="require(`@/assets/images/cards/${cardFront.id}.jpg`)" />
          </div>
          <div class="card-face card-back" v-if="cardBack != null">
            <img :src="require(`@/assets/images/cards/${cardBack.id}.jpg`)" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
import { reactive, watch, inject } from "vue";
import { useRouter } from "vue-router";
import useEventBus from "@/utility/event-bus.js";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Card",
  components: {},
  props: { cardFront: null, cardBack: null },

  setup(props) {
    // console.log(props);
    // const { getCard } = inject("filters");
    const router = useRouter();
    const { bus } = useEventBus();
    const { currentCard, setCurrentCard } = inject("filters");
    const state = reactive({
      cardVertical: "dritta",
      cardOne: "start",
      cardFront: props.cardFront,
      cardBack: props.cardBack,
      cardId: null,
    });
    // const cardId = computed(() => {
    //   if (!state.cardFront) return "";
    //   return state.cardOne == "start" ? state.cardFront.id : state.cardBack.id;
    // });

    const sound = new Audio(require("@/assets/sounds/carddrop.mp3"));
    const rotateHorizontal = () => {
      state.cardOne == "start"
        ? (state.cardOne = "flipped")
        : (state.cardOne = "start");
      state.cardId =
        state.cardOne == "start" ? state.cardFront.id : state.cardBack.id;
      setCurrentCard(state.cardId);
      // console.debug("rotateHorizontal", sound);
      sound.play();
      // console.debug("rotateHorizontal", sound);
    };
    //   cardFront: computed(() => getCard(router.currentRoute.value.params?.id)),
    //   cardBack: computed(() => {
    //     //   const idCardFront = router.currentRoute.value.params?.id;
    //     const idCardFront = props.cardFront?.id;
    //     if (!idCardFront) return null;
    //     const idCardBack =
    //       idCardFront.slice(-1) === "F"
    //         ? idCardFront.slice(0, -1) + "R"
    //         : idCardFront.slice(0, -1) + "F";
    //     console.log("FlipCard!", idCardFront, idCardBack);
    //     return getCard(idCardBack);
    //   }),

    // function rotateCard() {
    //   console.debug("rotateCard");
    // }
    watch(
      () => bus.value.get("rotateRadiator"),
      () => {
        console.debug("rotateRadiator", currentCard.value, props.cardFront.id);
        if (currentCard.value == props.cardFront.id) {
          state.cardVertical == "dritta"
            ? (state.cardVertical = "rovesciata")
            : (state.cardVertical = "dritta");
          sound.play();
        }
      }
    );

    return {
      state,
      router,
      // cardId,
      rotateHorizontal,
      // rotateCard,
    };
  },
};
</script>
  
